<template>
	<div id="jobService">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">发布招聘求职</div>
        <!--<div class="van-nav-bar__right color_40a3f3">-->
          <!--5/30-->
        <!--</div>-->
      </div>
    </div>
    <div class="login_box">
      <div style="width: 96%;height: 30px;border-radius: 5px;margin-left: 2%;line-height: 30px" class="text_center">
        <span class="head_span" style="background-color: #c10804;color: #ffffff" v-show="show">招聘</span>
        <span class="head_span" style="background-color: #e5e5e5;color: #8b8b8b" v-show="!show" @click="reOpen(5)">招聘</span>
        <span class="head_span" style="background-color: #c10804;color: #ffffff" v-show="!show">求职</span>
        <span class="head_span" style="background-color: #e5e5e5;color: #8b8b8b" v-show="show" @click="reOpen(6)">求职</span>
      </div>
      <div v-show="show1">
        <van-list v-model="loading" :finished="finished" @load="getJobList">
          <div style="margin-top: 5px" v-for="(item,index) in JobList">
            <div class="text_center font_12 color_8b8b8b">
              <span>{{item.addtime}}</span>
            </div>
            <div class="background_color_ffffff font_12" style="margin: 0 10px">
              <div class="text_right" style="border-bottom: 1px solid #eeeeee;padding: 6px">
                <span style="float: left;margin-top: 4px;">{{item.label}}</span>
                <!--<img src="static/img/icon_bj_wdwx.png" height="20px"/>-->
                <img src="static/img/icon_shanchu_wdwx.png" height="16px" @click="delService(item)"/>
              </div>
              <div style="padding: 6px">
                <span class="color_8b8b8b">{{item.content}}</span>
              </div>
              <div style="padding: 6px;text-align: right">
                <span class="color_c10804" @click="toJobInfo(item)">查看详情</span>
              </div>
            </div>
          </div>
        </van-list>
      </div>

      <div v-show="show2">
        <van-list v-model="loading" :finished="finished" @load="getJobList">
          <div style="margin-top: 5px" v-for="(item,index) in JobList">
            <div class="text_center font_12 color_8b8b8b">
              <span>{{item.addtime}}</span>
            </div>
            <div class="background_color_ffffff font_12" style="margin: 0 10px">
              <div class="text_right" style="border-bottom: 1px solid #eeeeee;padding: 6px">
                <span style="float: left;margin-top: 4px;">{{item.label}}</span>
                <!--<img src="static/img/icon_bj_wdwx.png" height="20px"/>-->
                <img src="static/img/icon_shanchu_wdwx.png" height="16px" @click="delService1(item)"/>
              </div>
              <div style="padding: 6px">
                <span class="color_8b8b8b">{{item.content}}</span>
              </div>
              <div style="padding: 6px;text-align: right">
                <span class="color_c10804" @click="toResume(item)">查看详情</span>
              </div>
            </div>
          </div>
        </van-list>
      </div>
      <div class="text_center background_color_ffffff add_bottom">
        <img src="static/img/icon_tj1.png" width="15px" style="vertical-align: middle;"/>
        <span class="font_12 color_c10804" @click="toJob">添加发布</span>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'jobService',
		data() {
			return {
        show:true,
        show1:true,
        show2:false,
        JobList:[],
        userId:localStorage.getItem('userId'),
        type: 5,
        loading: false,
        finished: false,
        pageNum: 1,
			}
		},
    mounted() {
      this.getJobList();
    },
		methods:{
      delService1(dataMap){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:dataMap.samecityid
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/deletesamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.getJobList(6);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      delService(dataMap){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:dataMap.samecityid
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/deletesamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.getJobList(5);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      getJobList(){
        this.loading = true
        let $this = this;
        let obj = {
          userid: $this.userId,
          type:this.type,
          pageNum: $this.pageNum
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecitylist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.JobList = $this.JobList.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
            // $this.JobList = rpdata.data.data.data;
          }
        })
        .catch(function(err) {})
      },
      reOpen(dataMap){
        this.show = !this.show;
        this.show1 = !this.show1;
        this.show2 = !this.show2;
        this.type=dataMap;
        this.pageNum = 1;
        this.JobList = [];
        this.finished = false;
        this.loading = false;
        this.getJobList();
      },
      toJob(){
        // this.$router.push('Job');
        if(this.show2){
          this.$router.push('addResume')
        }else{
          this.$router.push('addJob')
        }
      },
      toResume(dataMap){
        this.$store.commit('setResumeInfo', dataMap);
        this.$router.push('resume');
      },
      //招聘详情
      toJobInfo(dataMap){
        this.$store.commit('setJobInfo', dataMap);
        this.$router.push('JobInfo');
        // this.$router.push({path:'JobInfo',query:{data:dataMap}});
      },
		}
	}
</script>
<style scoped>
  .head_span {
    display: inline-block;
    width: 48%;
    border-radius: 5px;
  }
  .login_box{
    margin-top: 50px;
    margin-bottom: 45px;
  }
</style>



